@import url(//fonts.googleapis.com/css?family=Lato:300:400);

.parentContainer {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #fff;
}
.mb50 {
  margin-bottom: 50px;
}
.glass {
  /* background: linear-gradient(60deg, rgba(84,58,183,1) 0%,  100%); */
  background-color: #077395;
  width: 30vw;
  height: 65vh;
  position: relative;
  -webkit-border-top-left-radius: 10px;
  -webkit-border-top-right-radius: 10px;
  -moz-border-radius-topleft: 10px;
  -moz-border-radius-topright: 10px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: contain;
  box-shadow: 17px 17px 20px 12px rgba(215, 213, 213, 0.73);
  -webkit-box-shadow: 17px 17px 20px 12px rgba(215, 213, 213, 0.73);
  -moz-box-shadow: 17px 17px 20px 12px rgba(215, 213, 213, 0.73);
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.button {
  border: none;
  color: #fff;
  text-align: center;
  text-decoration: none;
  font-size: 20px;
  margin: 4px 2px;
  cursor: pointer;
  width: 120px;
  border-radius: 10px;
  text-align: center;
  padding: 10px;
  height: 50px;
}

body {
  margin: 0;
}

h1 {
  font-family: "Lato", sans-serif;
  font-weight: 300;
  letter-spacing: 2px;
  font-size: 48px;
}
p {
  font-family: "Lato", sans-serif;
  letter-spacing: 1px;
  font-size: 14px;
  color: #333333;
}

.header {
  position: relative;
  text-align: center;
  color: white;
}

.waves {
  position: relative;
  width: 30vw;
  height: 10vh;
  margin-bottom: -7px; /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
}

.content {
  position: relative;
  height: 20vh;
  text-align: center;
  background-color: white;
}

/* Animation */

.parallax > use {
  animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.parallax > use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.parallax > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.parallax > use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}
@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}

/*Shrinking for mobile*/
@media (max-width: 768px) {
  .waves {
    height: 50px;
    min-height: 40px;
    width: 40vw;
  }
  .button {
    width: 90px;
    font-size: large;
    height: fit-content;
  }
  .glass {
    width: 40vw;
    height: 22vh;
  }
  .content {
    height: 30vh;
  }
  h1 {
    font-size: 24px;
  }
}
